import { Component, OnInit, OnDestroy } from '@angular/core';
import * as _ from 'lodash';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { ExcelService } from '../../../core/excel/excel.service';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { TipoClientePopUpComponent } from '../../commonPopUp/tipoClientePopUp/tipoClientePopUp.component';
import { ClientesService } from '../../../core/consultaService/clientes.service';
import { ConsultasService } from '../../../core/consultaService/consultas.service';
import { ZonasTodasPopUpComponent } from '../../commonPopUp/zonasTodasPopUp/zonasTodasPopUp.component';
import { AuthService } from '../../../core/consultaService/auth.service';
import { DatePipe } from '@angular/common';
import { NgxSpinnerService } from 'ngx-spinner';
import { UtilService } from '../../../services/util.service';

const swal = require('sweetalert');
declare var jQuery: any;
declare var $: any;


@Component({
  selector: 'app-listadoCliente',
  templateUrl: './listadoCliente.component.html',
  styleUrls: ['./listadoCliente.component.scss']
})
export class ListadoClienteComponent implements OnInit, OnDestroy {

  fechaActual =  new Date();
  forma: FormGroup;
  tiposClienteModal: BsModalRef;
  zonasTodasModal: BsModalRef;
  territoriosTodosModal: BsModalRef;
  subData: any;
  subData2: any;
  origen: string;
  user: any = {};
  rutoperador: string;
  clientes: Array <any> = [];
  request: any = {};
  tipoClientes: Array<any> = [
    {
      tipoCliente: null,
      descTipo: '(Todos)'
    },
    {
      tipoCliente: 'EFECT',
      descTipo: 'Efectivo'
    },
    {
      tipoCliente: 'ACTIV',
      descTipo: 'Activos'
    }
  ];
  fechaDesdeMod: any;
  fechaHastaMod: any;

  constructor(private excelService: ExcelService, fb: FormBuilder,
    private modalService: BsModalService, private setearDatos: ConsultasService,
    private authService: AuthService, private datePipe: DatePipe, private utilService: UtilService,
    private clienteService: ClientesService, public loader: NgxSpinnerService) {
    this.forma = new FormGroup ({
      codTipoCliente: new FormControl(''),
      tipoCliente: new FormControl(''),
      fechaDesde: new FormControl(''),
      fechaHasta: new FormControl(''),
      codZona: new FormControl({value: '', disabled: true }),
      zona: new FormControl({value: '', disabled: true }),
      codTerritorio: new FormControl({value: '', disabled: true }),
      territorio: new FormControl({value: '', disabled: true })
  });
   }
   openModalWithComponent() {
    this.tiposClienteModal = this.modalService.show(TipoClientePopUpComponent, {backdrop: 'static', keyboard: false});
  }

  ngOnInit() {
    this.user = this.authService.getDatosUser();
    this.forma.controls.fechaDesde.setValue(this.utilService.dateToStringFormat(this.fechaActual));
    this.forma.controls.fechaHasta.setValue(this.utilService.dateToStringFormat(this.fechaActual));

    this.subData = this.setearDatos.datos$.subscribe(resData => {
      this.setTipo(resData);
    });
    this.subData2 = this.setearDatos.datosGeo$.subscribe(resData => {
      this.setTipo(resData);
    });
  }
  ngOnDestroy() {
    if (this.subData !== undefined) {
      this.subData.unsubscribe();
    }
    if (this.subData2 !== undefined) {
      this.subData2.unsubscribe();
    }
  }

  openModalZonasTodas() {
    this.origen = 'zonasTodas';
    const initialState = {
      parametro: this.user.nrutoperador,
      origen: 'zonasTodas'
    };
    this.zonasTodasModal = this.modalService.show(ZonasTodasPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }
  openModalTerritoriosTodos() {
    this.origen = 'territorios';
    const initialState = {
      parametro: this.user.nrutoperador,
      parametroDos: this.forma.controls.codZona.value,
      origen: 'territorios'
    };
    this.territoriosTodosModal = this.modalService.show(ZonasTodasPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  setTipo(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case 'tipoCliente': {
          this.forma.controls.tipoCliente.setValue(resData.par_deslargo01);
          this.forma.controls.codTipoCliente.setValue(resData.par_codparametro01);
          break;
        }
        case 'zonasTodas': {
          this.forma.controls.codZona.setValue(resData.zon_scodzona);
          this.forma.controls.zona.setValue(resData.zon_sdeszona);
          this.forma.controls.codTerritorio.setValue('');
          this.forma.controls.territorio.setValue('');
          break;
        }
        case 'territorios': {
          this.forma.controls.codTerritorio.setValue(resData.ter_scodterritorio);
          this.forma.controls.territorio.setValue(resData.ter_sdesterritorio);
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }

  editExcel (clientes) {

    let headerExcel = [
      "Fecha Generacin Reporte",
      "Operador (Descripcin)",
      "Cdigo Zona",
      "Descripcin de Zona",
      "Cdigo Territorio",
      "Descripcin de Territorio",
      "Cdigo Cliente",
      "Local Cliente",
      "Cdigo Relacin Comercial Cliente",
      "Nombre Cliente",
      "Descripcin Direccin de Visita",
      "Ciudad de Direccin de Visita",
      "Comuna de Direccin de Visita",
      "Descripcin Direccin de Entrega",
      "Ciudad de Direccin de Entrega",
      "Comuna de Direccin de Entrega",
      "Descripcin Direccin de Pago",
      "Ciudad de Direccin de Pago",
      "Comuna de Direccin de Pago",
      "Frecuencia Visita",
      "Lunes",
      "Martes",
      "Miercoles",
      "Jueves",
      "Viernes",
      "Sabado",
      "Domingo",
      "Fecha Bloqueo",
      "Motivo Bloqueo",
      "Indicador Bloqueo",
      "Condicin de Pago (Codigo)",
      "Condicin de Pago (Descripcin)",
      "Dias - Plazo",
      "Forma de Pago (Codigo)",
      "Forma de Pago (Descripcin)",
      "Fecha Creacin ",
      "Fecha ltima venta"];

      let dataExcel = [];
      for(let i in clientes) {
        let cliente = [
          this.utilService.dateToLocalStringFormat(this.fechaActual),
          this.clientes[i].ope_snombre,
          this.clientes[i].ter_scodzona,
          this.clientes[i].zon_sdeszona,
          this.clientes[i].ter_scodterritorio,
          this.clientes[i].ter_sdesterritorio,
          `${(this.clientes[i].cli_nrutcliente) + (this.clientes[i].cli_sdigverificador)}`,
          this.clientes[i].rco_nnumlocal,
          this.clientes[i].rco_scodrelacioncomercial,
          `${(this.clientes[i].cli_snombre) + (this.clientes[i].cli_sapepaterno)}`,
          this.clientes[i].visita_dic_sdireccion,
          this.clientes[i].visita_dic_sdesciudad,
          this.clientes[i].visita_dic_sdescomuna,
          //entrega
          this.clientes[i].entrega_dic_sdireccion,
          this.clientes[i].entrega_dic_sdesciudad,
          this.clientes[i].entrega_dic_sdescomuna,
          //direccion pago
          this.clientes[i].pago_dic_sdireccion,
          this.clientes[i].pago_dic_sdesciudad,
          this.clientes[i].pago_dic_sdescomuna,
          this.clientes[i].desfrecuencia,
          //dias de visita
          this.clientes[i].sdesrutalunes,
          this.clientes[i].sdesrutamartes,
          this.clientes[i].sdesrutamiercoles,
          this.clientes[i].sdesrutajueves,
          this.clientes[i].sdesrutaviernes,
          this.clientes[i].sdesrutasabado,
          this.clientes[i].sdesrutadomingo,
          //bloqueo
          this.utilService.dateStringServiceToStringFormat(this.clientes[i].cli_dfhoestado),
          this.clientes[i].desmotivobloqueo,
          `${(this.clientes[i].cli_sindbloqueo === 'N' ? "No Bloqueado": "Bloqueado")}`,
          //cobro
          this.clientes[i].rco_scodtipocobro,
          this.clientes[i].destipocobro,
          this.clientes[i].rco_ndiascredito,
          //pago
          this.clientes[i].rco_scodtipocobro,
          this.clientes[i].destipocobro,
          this.utilService.dateStringServiceToStringFormat(this.clientes[i].cli_dfhocreacion),
          this.utilService.dateStringServiceToStringFormat(this.clientes[i].rco_dfhoultimaventa),

        ]
        dataExcel.push(cliente);
      }
      let fechaExcel = this.utilService.dateToLocalStringFormat(this.fechaActual);
      let titulo = `Listado de Clientes_${this.utilService.dateToStringDateFullFormat(new Date())}00`;
      this.excelService.generateExcel(titulo, headerExcel, dataExcel, fechaExcel);
      swal('Perfecto', 'Su reporte ha sido generado con exito!', 'success');

  }

  generateExcel() {
    this.loader.show();
    this.request.codTerritorio = this.forma.controls.codTerritorio.value;
    this.request.codZona = this.forma.controls.codZona.value;
    this.fechaDesdeMod = (this.forma.controls.fechaDesde.value).replace(/-/g, '') + '00000000';
    this.forma.controls.fechaDesde.setValue(this.fechaDesdeMod);
    this.fechaHastaMod = (this.forma.controls.fechaHasta.value).replace(/-/g, '') + '23595999';
    this.forma.controls.fechaHasta.setValue(this.fechaHastaMod);
    this.request.fechaDesde = this.forma.controls.fechaDesde.value;
    this.request.fechaHasta = this.forma.controls.fechaHasta.value;
    this.request.rutOperador = this.user.nrutoperador;
    this.request.tipoCliente = this.forma.controls.tipoCliente.value;
    this.clienteService.getInfoClientes(this.request)
          .subscribe((data: any) => {
            this.loader.hide();
            (this.clientes = data.clienteOperador);
            if (data.clienteOperador) {
              if (data.clienteOperador.length === 0 && data.error.codError === 0 ){
                swal('Atencin', 'No se encontraron resultados', 'info');
              } else {
                this.editExcel(this.clientes);
              }
            }
            if (data.error.codError !== 0) {
                swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
            }
            this.fechaDesdeMod = '';
            this.fechaHastaMod = '';
        },
        error => {
            this.loader.hide();
            swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
        });
  }


}
